import React from 'react';
import mDate from './date-utils.js';

let lastDate = "";

function Dategroup(props){
  let {date, bg, text, pd, w, float} = props
  let todayDate = mDate(new Date()).getDate();  
  let msgDate = mDate(date).getDate();  
  const mystyle = {
    float: float,
    color: text || '#fff',
    backgroundColor: bg || '#84a29e',
    padding:  pd || "2px",
    position: '-webkit-sticky',
    position: 'sticky',
    top: '10px',
    zIndex: 0,
    minWidth: '100px',
    textAlign: 'center',
    margin:'0 auto',
    width: w || '100px',
  };
  if (lastDate !== msgDate) {
    let displayDate = msgDate === todayDate? 'Today' : msgDate;
    lastDate = msgDate;
    return (
        <div className="displayDate mx-auto rounded" style={mystyle}>
          {displayDate}
        </div>
      );
  }
  else{ return null}
}

export default  Dategroup;