
const mDate = (dateString) => {	
	let date = dateString ? new Date(dateString) : new Date();

	let dualize = (x) => x < 10 ? "0" + x : x;
	let getTime = () => dualize(date.getHours()) + ":" + dualize(date.getMinutes());

	let getDate = () => dualize(date.getDate()) + "/" + dualize(date.getMonth()) + "/" + dualize(date.getFullYear());

	function getAMPM() {
	  var hours = date.getHours();
	  var minutes = date.getMinutes();
	  var ampm = hours >= 12 ? 'pm' : 'am';
	  hours = hours % 12;
	  hours = hours ? hours : 12; // the hour '0' should be '12'
	  minutes = minutes < 10 ? '0'+minutes : minutes;
	  var strTime = hours + ':' + minutes + ' ' + ampm;
	  return strTime;
	}
	let lastDate = '';
	function getDateName(dateString){
	  let todayDate = mDate(new Date()).getDate();  
	  let msgDate = mDate(date).getDate();  
	  if (lastDate !== msgDate) {
	    let displayDate = msgDate === todayDate? 'Today' : msgDate;
	    lastDate = msgDate;
	    return displayDate;
	  }
	  else{ return ''}	
	}


	return {
		subtract: (otherDateString) => {
			return date - new Date(otherDateString);
		},
		lastSeenFormat: () => {
			let dateDiff = Math.round(new Date() - date) / (1000 * 60 * 60 * 24);
			let value = (dateDiff === 0) ? "Today" : (dateDiff === 1) ? "Yesterday" : getDate();
			return value + " at " + getTime();
		},
		getDate: () => {
			return getDate();
		},
		getTime: () => {
			return getTime();
		},
		getAMPM: () => {
			return getAMPM();
		},
		getDateName: () => {
			return getDateName();
		},
		toString:() => {
			return date.toString().substr(4, 20);
		},
	};
};



export default mDate;